import React from "react";
import Layout from "../components/Layout";
import "./Upload.css";
import { useState, useEffect } from "react";
import LoadImage from "./LoadImage";
import Removebg from "../components/Removebg";
import Display from "../components/Display";
// import { TailSpin } from "react-loader-spinner";
import Loading from "../components/Loading"
import { useUserAuth } from "../context/UserAuthContext";
import {
  getStorage,
  ref,
  uploadBytes,
  getDownloadURL,
  uploadBytesResumable,
} from "firebase/storage";
import { storage } from "../FirebaseConfig";
import { db } from "../FirebaseConfig";
import { v4 as uuid } from 'uuid';
import { doc, setDoc, updateDoc, arrayUnion } from "firebase/firestore";
export default function Upload() {
  // const [img, setImg] = useState("");
  const [imgPredict, setImgPredict] = useState("");
  const [loading, setLoading] = useState(false);
  const [imgUpload, setImgUpload] = useState(null);
  const [imgSrc, setImgSrc] = useState();
  const { user } = useUserAuth();
  const [urlDownload, getUrlDownload] = useState();
  // upload image to firebase

  const uploadImageFile = async () => {
    //
    if (!imgUpload) return;
    if (!user) return;
    const unique_id = uuid();
    let nameImgSave = unique_id + imgUpload.name
    const sotrageRef = ref(storage, `images/${nameImgSave}`);
    const uploadTask = uploadBytesResumable(sotrageRef, imgUpload);

    uploadTask.on(
      "state_changed",
      (snapshot) => {
        // Observe state change events such as progress, pause, and resume
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        const progress =
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log("Upload is " + progress + "% done");
        switch (snapshot.state) {
          case "paused":
            console.log("Upload is paused");
            break;
          case "running":
            console.log("Upload is running");
            break;
        }
      },
      (error) => {
        // Handle unsuccessful uploads
      },
      () => {
        // Handle successful uploads on complete
        // For instance, get the download URL: https://firebasestorage.googleapis.com/...
        getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
          // console.log("File available at", downloadURL);
          getUrlDownload(downloadURL);
         
        });
      }
    );
    if(urlDownload){
      const updateData = doc(db, "users", user.email);
      await updateDoc(updateData, {
        srcImgPaths: arrayUnion(urlDownload)
    })
    }
  };

  useEffect(() => {
    uploadImageFile();
  }, [imgUpload]);

  // const [cacheImg, setcacheImg] = useState("");
  // const [cacheImgRm, setcacheImgRm] = useState("");
  // const getCacheImage = async ()=>{
  //   const cacheStorage = await caches.open("data");
  //   const cachedResponse = await cacheStorage.match("src");
  //   cachedResponse.json().then((item) => {
  //     let tmp = "data:image/png;base64," + item.uploadResponse.data.encoded_img
  //     let tmpRmBg = "data:image/png;base64," + item.uploadResponse.data.predict
  //     setcacheImg(tmp)
  //     setcacheImgRm(tmpRmBg)
  //   });
  // }
  // useEffect(()=>{
  //   getCacheImage()
  // },[img])
  // console.log(cacheImg)
  return (
    <Layout>
      <div className="container-upload">
        <h2
          style={{
            color: "rgb(84, 97, 108)",
            fontSize: "30px",
            margin: "1rem auto",
          }}
        >
          Upload an image to remove the background{" "}
        </h2>
        <div className="upload">
          <LoadImage
            setImgPredict={setImgPredict}
            loading={loading}
            setLoading={setLoading}
            setImgSrc={setImgSrc}
            setImgUpload={setImgUpload}
          />
        </div>
      </div>

      {loading && (
        <Loading />
      )}
      {imgSrc && (
        <div className="display-container">
          <Display
            imgSrc={imgSrc}
            imgPredict={imgPredict}
            setImgSrc={setImgSrc}
          />
        </div>
      )}
      {/* { img==="" && cacheImg && <div className="display-container">
          <Display imgSrc={ cacheImg} imgPredict={ cacheImgRm} setImg={setImg} />
        </div> } */}

      {imgSrc && (
        <h2
          style={{
            width: "60%",
            margin: "1rem auto",
            textAlign: "center",
            fontSize: "30px",
            color: "rgb(84, 97, 108)",
          }}
        >
          Image Preview
        </h2>
      )}
      {imgSrc && (
        <div className="upload-show">
          <div className="upload-btn">
            <Removebg imgOrigin={imgSrc} imgRmBg={imgPredict} />
          </div>
        </div>
      )}
    </Layout>
  );
}
